"use client";
import { gql } from "@apollo/client";
import { useQuery } from "@apollo/client/react";
import { useAuthenticationStatus } from "@nhost/nextjs";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

export const dynamic = 'force-dynamic';
export const runtime = 'edge';

interface Board {
  id: string;
  name: string;
  created_at: string;
}

const BOARDS = gql`
  query Boards {
    boards(order_by: { created_at: desc }) {
      id
      name
      created_at
    }
  }
`;

export default function BoardsPage() {
  const { isAuthenticated } = useAuthenticationStatus();
  const { data, loading, error } = useQuery<{ boards: Board[] }>(BOARDS, {
    skip: !isAuthenticated,
  });

  if (!isAuthenticated) {
    return (
      <main className="p-6">
        <p>Please sign in to view your boards.</p>
      </main>
    );
  }

  if (loading) {
    return (
      <main className="p-6">
        <h1 className="text-2xl font-semibold mb-4">Boards</h1>
        <p>Loading your boards...</p>
      </main>
    );
  }

  if (error) {
    return (
      <main className="p-6">
        <h1 className="text-2xl font-semibold mb-4">Boards</h1>
        <p className="text-red-600">Error loading boards: {error.message}</p>
      </main>
    );
  }

  const boards = data?.boards || [];

  return (
    <main className="p-6">
      <h1 className="text-2xl font-semibold mb-6">Boards</h1>
      {boards.length === 0 ? (
        <p className="text-muted-foreground">No boards found. Create your first board to get started.</p>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {boards.map((board) => (
            <Card key={board.id} className="hover:shadow-md transition-shadow">
              <CardHeader>
                <CardTitle className="text-lg">{board.name}</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-sm text-muted-foreground">
                  Created {new Date(board.created_at).toLocaleDateString()}
                </p>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </main>
  );
}
